<template>
    <v-app id="RegisterLogin">
        <RegistrationLogin @login="validateUser" @retry="retry" :errorStatus="errorStatus" :errorText="errorText" />
    </v-app>
</template>

<script>
// @ is an alias to /src
import RegistrationLogin from '../components/Registrationlogin.vue'
import axios from "../axios";

export default {
  name: 'Login',
  components: {
    RegistrationLogin
  },
  data(){
    return{
     error: true,
     errorStatus: 0,
     errorText: "Internal Server Error"
    }
  },
  methods: {
    retry: function()
    {
      this.errorStatus = 0;
      this.errorText = ""
    },
    validateUser(data){
      let self = this;
        if(data.accessToken === undefined || data.accessToken === null || data.accessToken === '')
        {
          self.errorStatus =  412;
          self.errorText =  "Password Required";
        }
        else if(data.phoneNumber === undefined || data.phoneNumber === null || data.phoneNumber === '')
        {
          self.errorStatus =  412;
          self.errorText =  "Phonenumber Required";
        }
        else
        {
          axios.post("/validateme", { "accessToken": data.accessToken, "phoneNumber": data.phoneNumber ,eventid: 'RAJYOTHSAVA_2019' })
              .then(function (response) {
                  if(response.status === 200){
                    self.$router.push("/registration/"+encodeURI([response.data.access_key, 
                    response.data.isadmin, 
                    response.data.allow_user_creation, 
                    response.data.allow_user_registration, 
                    response.data.allow_user_verification,
                    response.data.name].join("/")))
                    
                  }
                  else
                  {
                    self.errorStatus =  500;
                    self.errorText =  "Internal Error";
                  }
                })
                .catch(function (error) {
                  self.errorStatus =  error.response.status;
                  self.errorText =  error.response.statusText;
                });
        }
    }
  },
}
</script>
