<template>
 
    <v-content id="RegisterLogin">
      <v-container
        class="fill-height"
        fluid
      >
        <v-row
          align="center"
          justify="center"
        >
          <v-col  cols="12"     sm="8"      md="4" >
            <v-card class="elevation-12">
              <v-toolbar color="primary" dark flat>
                <v-toolbar-title>Enter your Access Code</v-toolbar-title>  
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    v-model="phoneNumber"
                    label="Mobile Number"
                    name="Phone"
                    prepend-icon="mdi-account "
                    type="text"
                    @keypress="validateOnlyNumbers"
                    class="headline font-weight-regular"
                  ></v-text-field>
                  <v-text-field
                    v-model="accessKey"
                    label="Password"
                    name="login"
                    prepend-icon="mdi-lock"
                    type="password"
                    class="headline font-weight-regular"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-chip
                  class="ma-2"
                  close
                  color="red"
                  text-color="white"
                  @click:close="retry"
                  v-show="errorStatus > 0"
                >
                <v-avatar left>
                  <v-icon>mdi-cancel</v-icon>
                </v-avatar>
                  {{errorText}}
                </v-chip>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="login( {'accessToken': accessKey, 'phoneNumber': phoneNumber} )" :disabled="errorStatus > 0">Login</v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
</template>

<script>
  export default {
    props: {
      errorText : String,
      errorStatus: Number
    },
    data: () => ({
      phoneNumber:"",
      accessKey:""
    }),
    methods: {
      login : function(data){
        this.$emit("login", data);
      },
      retry : function(){
        this.$emit("retry");
      },
      validateOnlyNumbers(event)
      {
        let keyCode = (event.keyCode ? event.keyCode : event.which);
        if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) { // 46 is dot
           event.preventDefault();
        }
      }
    },
  }
</script>