  <template>
 
    <v-content id="RegisterForm">
      <v-card-text>
                <v-form
                  ref="form"
                  lazy-validation
                >
                
                <v-row v-show="displaytoast">
                  <v-spacer></v-spacer>
                <v-chip
                  close
                  :color="isError ? 'red':'green'"
                  text-color="white"
                  @click:close="displaytoast = false"
                >
                  {{responseText}}
                </v-chip>
                 <v-spacer></v-spacer>
                </v-row>

                <v-row>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="primary"
                        class="mr-4"
                        @click="scanQrCode = !scanQrCode, infoAvail = false"
                        large
                        prepend-icon = "mdi-arrow-up-bold-box-outline"
                    >
                    {{scanQrCode ? 'Close QR-Scanner' : 'Scan QR Code'}}
                  </v-btn>
                  <v-spacer></v-spacer>

                </v-row>

                <qrcode-stream @init="onInit" @decode="onDecode" v-if="scanQrCode">
                </qrcode-stream>

                <v-row style="margin-top:1em" v-show="!infoAvail && !manual">
                  <v-spacer></v-spacer>
                  <v-progress-circular
                    indeterminate
                    color="primary"
                  ></v-progress-circular>
                  <v-spacer></v-spacer>
                </v-row>

                <v-row style="margin-top:1em" v-show="infoAvail && !scanQrCode">
                  <v-spacer></v-spacer>
                <v-simple-table dark>
                  <template v-slot:default>
                  <tbody>
                    <tr>
                      <td>Event</td>
                      <td>{{info.event}}</td>
                    </tr>
                    <tr>
                      <td>Name</td>
                      <td>{{info.name}}</td>
                    </tr>
                    <tr>
                      <td>Number of adults</td>
                      <td>{{info.numOfAdults}}</td>
                    </tr>
                    <tr>
                      <td>Number of Students</td>
                      <td>{{info.numOfStudents}}</td>
                    </tr>
                    <tr>
                      <td>Number of kids</td>
                      <td>{{info.numOfKids}}</td>
                    </tr>
                    <tr>
                      <td>Registered on</td>
                      <td>{{info.registeredOn}}</td>
                    </tr>
                    <tr>
                      <td>Registered by</td>
                      <td>{{info.registeredBy}}</td>
                    </tr>
                    <tr>
                      <td>Last Validated</td>
                      <td :style="info.validatedOn ? 'background-color:red' : 'background-color:#424242'">{{info.validatedOn}}</td>
                    </tr>
                    </tbody>
                  </template>
                </v-simple-table>
                <v-spacer></v-spacer>
                </v-row>
                <v-row v-show="!scanQrCode && manual">
                     <v-spacer></v-spacer>
                    <v-text-field
                        v-model="accessKey"
                        label="Access Code"
                        name="login"
                        prepend-icon="mdi-lock"
                        type="password"
                        class="headline font-weight-regular"
                    ></v-text-field>
                    <v-spacer></v-spacer>
                </v-row>

                <v-row  v-show="!scanQrCode && manual">   
                    <v-spacer></v-spacer>
                    <v-btn
                        color="primary"
                        class="mr-4"
                        @click="verifyAccessToken(accessKey)"
                        large
                    >
                        Validate Access Code
                    </v-btn>
                    <v-spacer></v-spacer>
                </v-row>

                </v-form>
              </v-card-text>
    </v-content>
</template>

<script>
import { QrcodeStream } from 'vue-qrcode-reader'
import axios from "../axios";

  export default {
    props: {
      loggedName: String,
      loggedId: String
    },
    components: { QrcodeStream },
    data: () => ({
      scanQrCode: false,
      accessKey:'',
      manual: true,
      infoAvail: false,
      info:{
        "event": undefined,
        "name": undefined,
        "numOfAdults": undefined,
        "numOfStudents": undefined,
        "numOfKids": undefined,
        "registeredOn": undefined,
        "registeredBy": undefined,
        "validatedOn" : undefined
      },
      isError: true,
      responseText: "",
      displaytoast: false,
    }),
    methods: {
      onDecode (result) {
        this.manual = false
        this.scanQrCode = false;
        this.verifyAccessToken(result)
      },
      async onInit (promise) {
        try {
          await promise
        } catch (error) {
          alert(error)
        } finally {
          this.loading = false
        }
      },
      parseMinutes: function(n) {
        if(n === null)
          return null
        else
        {
          var num = n;
          var hours = (num / 60);
          var rhours = Math.floor(hours);
          var minutes = (hours - rhours) * 60;
          var rminutes = Math.round(minutes);
          return rhours + " hour(s), " + rminutes + " minute(s).";
        }
      },
      verifyAccessToken: function(key){

        let self = this;
        axios.post("/verification/"+self.loggedId,
                    {
                      "eventid":"RAJYOTHSAVA_2019",
                      "accessToken" : key
                    })
        .then(function(response){
            if(response.data.isValid)
            {
              self.info.event = response.data.info.event;
              self.info.name = response.data.info.name;
              self.info.numOfAdults = response.data.info.numofadults;
              self.info.numOfKids = response.data.info.numofkids;
              self.info.numOfStudents = response.data.info.numofstudents;
              self.info.registeredOn = new Date(response.data.info.registeredon).toDateString();
              self.info.registeredBy = response.data.info.registeredby;
              self.info.validatedOn = self.parseMinutes(response.data.info.validatedon);
              self.infoAvail = true;
              self.manual = true;
            }
            else
            {
              self.info.event = 'NA';
              self.info.name = 'NA';
              self.info.numOfAdults = 'NA';
              self.info.numOfStudents = 'NA';
              self.info.numOfKids = 'NA';
              self.info.registeredOn = 'NA';
              self.info.registeredBy = 'NA';
              self.info.validatedOn = 'NA';
              self.infoAvail = true;
              self.manual = true;
            }
          })
        .catch( function(error){
            self.manual = true;
            self.responseText = error.response.data ? error.response.data : 'Internal Error';
            self.displaytoast = true;
        })

      }
    },
  }
</script>