  <template>
 
    <v-content id="RegisterForm">
      <v-card-text>
                <v-form
                  ref="form"
                  v-model="valid"
                  lazy-validation
                >
                <v-row v-show="displaytoast">
                  <v-spacer></v-spacer>
                <v-chip
                  close
                  :color="isError ? 'red':'green'"
                  text-color="white"
                  @click:close="displaytoast = false"
                >
                  {{responseText}}
                </v-chip>
                 <v-spacer></v-spacer>
                </v-row>

                  <v-text-field
                    v-model="name"
                    :counter="99"
                    label="Name"
                    required
                  ></v-text-field>

                  <v-text-field
                    v-model="email"
                    label="E-mail"
                    type="email"
                    required
                  ></v-text-field>

                  <v-text-field
                    v-model="phoneNumber"
                    label="Phone Number"
                    type="text"
                    @keypress="validateOnlyNumbers"
                    required
                  ></v-text-field>

                  <v-text-field
                    v-model="password"
                    label="Access Key"
                    required
                    @input="converttouppercase()"
                  >
                  </v-text-field>

                  <v-row>
                    <v-spacer></v-spacer>
                    <v-switch v-model="userRegistration" label="User Registration"></v-switch>
                    <v-spacer></v-spacer>
                    <v-switch v-model="userVerification" label="User Verification"></v-switch>
                    <v-spacer></v-spacer>
                    <v-row>
                      <v-spacer></v-spacer>
                      <v-switch v-model="userCreation" label="User creation"></v-switch>
                      <v-spacer></v-spacer>
                    </v-row>
                  </v-row>
           
                  <v-text-field
                    class="font-weight-medium center"
                    :value="loggedName"
                    label="User Authorized By"
                    readonly
                    disabled
                  ></v-text-field>
                  <v-row>
                   <v-spacer></v-spacer>
                  <v-btn
                    color="primary"
                    class="mr-4"
                    @click="adduser"
                    large
                  >
                    Add User
                  </v-btn>
                  <v-spacer></v-spacer>
                  </v-row>

                </v-form>
              </v-card-text>
    </v-content>
</template>

<script>
import axios from "../axios";
  export default {
    props: {
      loggedName: String,
      loggedId: String
    },
    data: () => ({
      userRegistration: true,
      userVerification: false,
      userCreation: false,
      password:"",
      valid:"",
      email:"",
      name:"",
      isError: false,
      responseText: "",
      displaytoast: false,
      phoneNumber:""
    }),
    methods: {
      isValidEmail: function(email){
        const emailRegexp = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
        return emailRegexp.test(email);
      },
      adduser: function()
      {
        let self = this;
        if(!self.isValidEmail(self.email))
        {
            self.isError = true;
            self.displaytoast = true;
            self.responseText = "Enter a valid email";
        }
        else if( (self.password && self.password.length < 8) || !self.password)
        {
            self.isError = true;
            self.displaytoast = true;
            self.responseText = "Access Key should be atleast 8 char";
        }
        else if( !self.userCreation && !self.userVerification && !self.userRegistration)
        {
            self.isError = true;
            self.displaytoast = true;
            self.responseText = "Should enable atleast one previlage";
        }
        else
        axios.post("/adduser/"+self.loggedId,
                    {
                      "eventid":"RAJYOTHSAVA_2019",
                      "accessKey" : self.password,
                      "email" : self.email,
                      "name": self.name,
                      "phoneNumber": self.phoneNumber,
                      "canAddUser": self.userCreation ? 1 : 0,
                      "canVerifyUser": self.userVerification ? 1 : 0,
                      "canRegisterUser": self.userRegistration ? 1 : 0
                    })
        .then(function(response){
            self.isError = false;
            self.displaytoast = true;
            self.responseText = response.data;
            self.name = "";
            self.email = "";
            self.password = "";
            self.phoneNumber="";
            self.userRegistration = true;
            self.userVerification = false;
            self.userCreation = false;
          })
        .catch( function(error){
            self.isError = true;
            self.displaytoast = true;
            self.responseText = error.response.data;
        })
      },
      validateOnlyNumbers(event)
      {
        let keyCode = (event.keyCode ? event.keyCode : event.which);
        if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) { // 46 is dot
           event.preventDefault();
        }
      },
      converttouppercase: function(){
        this.password = this.password.toUpperCase()
      }
    },
  }
</script>